[dir] form .formControl {
    margin-top: 15px;
}

form .formLabel {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    color: #000;
    font-size: 13px;
    font-weight: 600;
    text-transform: uppercase;
}

[dir] form .formLabel {
    margin-bottom: 5px;
}

.select {
    width: auto !important;
    height: auto !important;
}

.select div {
    height: auto !important;
    line-height: inherit !important;
}

[dir] .select div {
    padding: 0 !important;
}

.select button {
    width: auto !important;
    height: auto !important;
}

[dir] .select button {
    padding: 0 !important;
}

[dir=ltr] .select button {
    right: -26px !important;
}

[dir=rtl] .select button {
    left: -26px !important;
}

.select > div > div:nth-child(1) > div:nth-child(2) {
    top: 0 !important;
}



